<template>
    <div class="tower-page-box">
        <!--<canvas id="canvas" v-show="showCanvas" :width="gameWidth" :height="gameHeight"></canvas>-->
        <div class="content">
            <div class="landing" v-show="showLanding">
                <div class="action-1">
                    <img src="./assets/main-index-title.png" class="title swing">
                </div>
                <div class="action-2">
                    <img id="start" src="./assets/main-index-start.png" class="start" @click="startHandler">
                </div>
            </div>
            <div class="loading" v-show="showLoading">
                <div class="main"><img src="./assets/main-loading.gif">
                    <div class="progress">
                        <div class="title font-wenxue" v-text="loadPercent">0%</div>
                        <div class="bar">
                            <div class="sub">
                                <div class="percent" :style="{width: loadPercent}"></div>
                            </div>
                        </div>
                        <div class="text">加载中</div>
                    </div>
                </div>
            </div>

            <div id="modal" class="modal" v-show="showModal">
                <div class="mask"></div>
                <div class="js-modal-content modal-content">
                    <div class="main">
                        <div class="container">
                            <img src="./assets/main-modal-bg.png" class="bg">
                            <div class="modal-main">
                                <div id="over-modal" class="hide js-modal-card" v-show="showModal">
                                    <img src="./assets/main-modal-over.png" class="over-img">
                                    <div id="score" class="over-score font-wenxue" v-text="scoreText"></div>
                                    <div id="over-zero" class="hide" v-show="showModal">
                                        <div class="tip"><p>再来一次吧！</p>
                                            <img src="./assets/main-modal-again-b.png" class="over-button-b js-reload"
                                                 @click="reloadHandler">
                                            <img src="./assets/main-modal-invite-b.png" class="over-button-b js-invite">
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!--<div class="wxShare hide">-->
            <!--<img src="./assets/main-share-icon.png">-->
            <!--</div>-->
        </div>
    </div>
</template>

<script>
    import {browser,sdk} from "../../common/util/bitUtil";
    import TowerGame from './js/index'

    let game, canvasEl

    let gameWidth = window.innerWidth > 750 ? 750 : window.innerWidth
    let gameHeight = window.innerWidth > 750 ? 1334: window.innerHeight

    export default {
        name: 'home',
        /**
         * 组件的初始数据
         */
        data() {
            return {
                gameWidth: window.innerWidth,
                gameHeight: window.innerHeight,
                scoreText: 0,
                showModal: false,
                showCanvas: false,
                showLoading: true,
                showLanding: true,
                loadPercent: 0
            }

        },
        computed: {},
        components: {},

        /**
         * 组件的方法列表
         */
        methods: {
            gameReady(cb) {
                if(canvasEl){
                    canvasEl.parentNode.removeChild(canvasEl)
                }
                canvasEl = document.createElement('canvas')
                const id = 'canvas_' + (new Date()).getTime()
                canvasEl.id = id;
                canvasEl.width = this.gameWidth
                canvasEl.height = this.gameHeight

                this.$el.insertBefore(canvasEl, this.contentEl)
                const option = {
                    width: this.gameWidth,
                    height: this.gameHeight,
                    canvasId: id,
                    soundOn: true,
                    setGameScore: (s) => {
                        this.scoreText = s
                    },
                    setGameSuccess: (s) => {
                        // successCount = s
                    },
                    setGameFailed: (f) => {
                        if (f >= 3) {
                            game.hooks.end({
                                score: this.scoreText
                            })
                            this.showModal = true
                        }
                    }
                }
                game = TowerGame(option)

                game.hooks = {
                    start: function () {
                        console.log('hooks start')
                    },
                    end: function (params) {
                        console.log('hooks end', params)
                    }
                }

                game.load(() => {
                    game.playBgm()
                    game.init()
                    cb && cb()
                }, this.updateLoading)
            },
            updateLoading(status) {
                var success = status.success
                var total = status.total
                var failed = status.failed
                if (failed > 0 && !loadError) {
                    loadError = true
                    alert('加载失败 请刷新后重试')
                    return
                }
                var percent = parseInt((success / total) * 100);
                if (percent === 100) {
                    this.hideLoading()
                }
                percent = percent > 98 ? 98 : percent
                this.loadPercent = percent + '%'
            },
            hideLoading() {
                this.showCanvas = true
                setTimeout(() => {
                    this.showLoading = false
                }, 1000)
            },
            startHandler() {
                setTimeout(() => {
                    this.showLoading = false
                    this.showLanding = false
                }, 950)
                this.startGame()
            },
            startGame(){
                game.hooks.start()
                setTimeout(game.start, 400)
            },
            reloadHandler() {
                this.showModal = false
                this.showLanding = false
                this.gameReady(() => {
                    this.startGame()
                })
            }
        },
        mounted() {
            let query = this.$route.query || {}
            const gameInstance = game

            this.contentEl = this.$el.querySelector('.content')
            this.gameReady()

            if(browser.app){
                sdk.onAdCompletion(function(params){
                    console.log(params)
                })

                gameInstance.hooks.start = function () {
                    sdk.gameShowAd(query)
                }
            }
        }
    }
</script>
